.app {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bioScroller {
  overflow-y: auto;
  height: 100vh;
  width: 55%;
  background-color: black;
}

.bioContent {
  margin: 5vh 3vw;
  display: flex;
  min-height: 90vh;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  color: beige;
}

.bioContent::-webkit-scrollbar {
  display: none;
}

.socialContainer {
  display: flex;
  flex: 1;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.profile {
  width: 150px;
  height: 150px;
  align-self: center;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border: 6px solid white;
}

@media only screen and (max-width: 700px) {
  .hide-if-small {
      display: none;
  }
}

@media only screen and (min-width: 700px) {
  .show-if-small {
      display: none;
  }
}
